    height: 45px;
    width: 250px;
    border-radius: 25px;
    border-color: white;
    margin-top: 10%; 
    color: white;
    font-size: 25px;
    }
    .sobreestudio,
    .sobresalareuniao {
    display: block;
    margin-bottom: 5%;
    margin-top: 15%;
    margin-left: 5%; 
    font-size: 32px; 
    color: darkgrey;
    line-height: 1.5em;
    font-family: 'Forma DJR Deck' sans-serif;
    }
    .sobreestudio h2,
    .sobresalareuniao h2 {
    text-align: left;
    font-size: 38px; 
    color: black;
    }
    .sobreestudio button,
    .sobresalareuniao button {
    background: none;
    height: 50px;
    width: 300px;
    border-radius: 25px;
    border-color: black;
    font-size: 30px;
    color: darkgrey;
    margin-left: 5%;
    margin-top: 10%; 
    }
    .especificacaoestudio {
    line-height: 1.5em;
    margin-left: 5%; 
    }
    .especificacaoestudio li {
    line-height: 2em;
    font-size: 30px; 
    }
    .especificacaoestudio h2 {
    margin-top: 25%;
    margin-left: 0%; 
    color: black;
    font-size: 45px;
    }
    .agendamentocombotao {
    background-size: cover;
    background-image:url(${___CSS_LOADER_URL_REPLACEMENT_3___});
    width: 1440px; 
    height: 1440px;
    }
    .agendamentocombotao button {
    margin-top: 15%;
    margin-left: 5%; 
    background-color: #E5C167;
    border: none;
    font-size: 35px;
    width: 500px;
    height: 80px; 
    border-radius: 35px; 
    font-family: 'Forma DJR Banner', sans-serif;
    color: white;
    }
    .agendamentocombotao h1 {
    color: white;
    margin-left: 5%;
    margin-top: 25%;
    font-size: 65px;
    line-height: 1.5em;
    font-family: 'Forma DJR Banner', sans-serif;
    }
    .final {
    display: flex;
    color: black;
    }
    .final h2 {
    font-size: 50px;
    color: black;
    }
    .agendamento {
    display: block;
    text-align: left;
    }
    .agendamento label {
    font-size: 25px;  
    }
    .endeco {
    margin-left: 10%;
    margin-right: 20%; /* Ajustado para a nova resolução */
    }
    .endeco h1 {
    text-align: right;
    }
    .endeco label {
    text-decoration: none;
    text-align: left;
    color: black;
    font-size: 25px; 
    display: block;
    }
    .logos {
    display: block;
    width: 1440px;
    }
    .paracelular{
        display: none;
    }
    .logos img {
    margin-left: 2.5%;  
    margin-top: 2.5%;   
    margin-bottom: 0.5%;
    }
    .caixabtn {
    position: absolute;
    right: 50px;         
    bottom: 60px;     
    }
    .caixabtn button {
    position: fixed;
    border-radius: 24px; /* Ajustado para a nova resolução */
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_4___});
    border: none;
    object-fit: cover;
    text-decoration: none;
    width: 48px; /* Mantido */
    height: 48px; /* Mantido */
    }
    .arboretologo {
        display: block;
        position: absolute;
    }
    .arboretologocelular {
        display: none;
    }
}
.navegation-auto div:not(:last-child){
    margin-right: 40px;
}

